@use '../../Foundation/vars' as va;
@use '../../Foundation/mixin';

.p-news__item{
    padding:calc(14/va.$sp-design-width*100vw) 0;
    //gridのgapを使う事でコンポーネントにマージンを持たせない 
    display: grid;
    gap: calc(8/va.$pc-design-width*100vw);
    //タグについては幅を狭める
    @include mixin.mq(pc){
        //display: flex;
        //多分1列にする必要がある
        padding:calc(20/va.$pc-design-width*100vw) 0; 
    }
}